.hero-selection-container{
  overflow: auto;
  width: 800px;
}

.hero-selection-component{ 
  display: flex;
  width: calc(99% - 1px);
  height: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;
}

.main-selection-container{
  overflow: auto;
  height: 100%;
}

.view-heroes-container{
  display: flex;
  flex-direction: row;
  list-style-type: none;
  padding-left: 25px;
  width: 800px;
  column-gap: 20px;
  padding:10px
}

.hero-selection{
  cursor:pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  column-gap: 15px;
  width: fit-content;
  transition: all 200ms ease-in;
}

.hero-selection:hover {
  transform: scale(1.1);
}

.view-selected-hero-container{
  width: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}

.view-hero-name{
  font-size: 25px; 
  font-weight: 700;
}



